import { Formik } from 'formik'
import React, { useState } from 'react'
import loader from '../../methods/loader'
import ApiClient from '../../methods/api/apiClient';
import { useHistory } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { ToastContainer, toast } from 'react-toastify';
export default function SignUp() {
  const navigate = useHistory()

  const Virtual= '64e83a928599356bddc2fa00';
  const employeerid = '64e5cdfc5bb9a041a39046d5'
  const [role,setrole]=useState(Virtual)

  const [eyes, setEyes] = useState({ password: false, confirmPassword: false, currentPassword: false });

  return (<>
  
    <div className="main_signup">
      <div className="row align-items-center justify-content-center ">
        <div className="col-md-12 p-0">
        <div className='top_section'>
          <div className='right_side'>

          <div className='logo_image'>
                  <img src='/assets/img/Logo_new.png' className='logo_name'/>
                </div>
           

            <Formik
              enableReinitialize
              initialValues={{
                email: "",
                firstname: "",
                lastname: "",
                password: "",
                role: "",
                check:"",
              
              }}
              onSubmit={(values) => {
               
                const payload = { email: values.email, password: values.password, firstName: values.firstname, lastName: values.lastname, fullName:`${values.firstname} ${values.lastname}`, role: role, }

                console.log("values",values)
                if(!values.check.length){
                  toast.error("Please Accepted The Terms Of Use And Privacy Policy")
                  return
                }
                
                loader(true);
                ApiClient.post(`register`, payload).then(res => {
                  if (res.message) {
                    navigate.push("/login?message="+res.message)
                  }
                })
              }}
            >{
                ({ values, handleChange, handleBlur, handleSubmit }) => (
                  <form className='centerLogin' onSubmit={handleSubmit}>
                    <div className="text-center mb-2">
                      <h3 className="text-center lgtext">Create your account.</h3>
                      <p className='accopunt text-center'>Already have an account? <Link class="sign_up" to="/login" > Sign In</Link></p>
                    </div>

                    {/* tab */}

                    <div className="text-center"></div>

                    <ul class="nav nav-tabs mb-2 justify-content-center d-flex" id="myTab" role="tablist">
  <li class="nav-item" role="presentation">
    <button class="nav-link active" id="home-tab" data-bs-toggle="tab" data-bs-target="#home" type="button" role="tab" aria-controls="home" aria-selected="true" onClick={(e) => setrole(employeerid)}>Employer</button>
  </li>
  <li class="nav-item" role="presentation">
    <button class="nav-link" id="profile-tab" data-bs-toggle="tab" data-bs-target="#profile" type="button" role="tab" aria-controls="profile" aria-selected="false" onClick={(e) => setrole(Virtual)}>Virtual Assistant</button>
  </li>
</ul>
<div class="tab-content" id="myTabContent">
  <div class="tab-pane fade show active" id="home" role="tabpanel" aria-labelledby="home-tab">   
  <div className=''>
  
                      <label className='form-label ml-2'>Email<span className='text-danger'>*</span></label>
                      <input type='email' value={values.email} required name='email' onChange={handleChange} onBlur={handleBlur} placeholder='Email Address' className='form-control bginput chnages' />
                
                    <div className='row mt-3'>
                      <div className='col-md-6'>
                        <label className='form-label ml-2'>First Name<span className='text-danger'>*</span></label>
                        <input type='text' value={values.firstname} required name='firstname' placeholder='First Name' onChange={handleChange} onBlur={handleBlur} className='form-control bginput chnages' />
                      </div>
                      <div className='col-md-6'>
                      <label className='form-label ml-2'>Last Name<span className='text-danger'>*</span></label>
                        <input type='text' value={values.lastname} required name='lastname' onChange={handleChange} onBlur={handleBlur} placeholder='Last Name' className='form-control bginput chnages' />
                      </div>
                    </div>
                    
                    <div className='col-md-12 p-0 mt-3 inputWrapper'>
                      <label className='form-label ml-2'>Password<span className='text-danger'>*</span></label>
                      <input className='form-control mb-0 bginput chnages' type={!eyes.password?'password':'text'} required value={values.password} name='password' minLength="8" onChange={handleChange} placeholder='Password' onBlur={handleBlur} />
                      <i className={eyes.password ? 'fa fa-eye' : 'fa fa-eye-slash'} onClick={() => setEyes({ ...eyes, password: !eyes.password })}></i>
                    </div>

                    <div className='mt-4 d-flex align-items-baseline'>
                      <input type='checkbox' className='checkBox' name="check" onChange={handleChange} />
                      <label className='clickBox ml-2 mb-0' >By clicking Create account, I agree that I have read and accepted the Terms of Use and Privacy Policy.</label>
                    </div>
                    <div>
                      <button type='submit' className='btn btn-primary loginclass mt-3'>Sign Up</button>
                    </div>
                    <div className='borderCls mt-3'><span className='or'>or</span></div>
                    <div className='text-center d-flex justify-content-center mt-2'>
                <a className='btn btn-outline google_id mr-3 '>
                  <img src='/assets/img/gogle.png' />
                  <span className='google_heading'></span>
                </a>
                <a className='btn btn-outline google_id'>
                  <img src='/assets/img/facebooklogo.png' />
                  <span className='google_heading'></span>
                </a>
                </div>
                   
                    </div>
                    
                    </div>
  <div class="tab-pane fade" id="profile" role="tabpanel" aria-labelledby="profile-tab">  
   <div className=''>
                      <label className='form-label ml-2'>Email<span className='text-danger'>*</span></label>
                      <input type='email' value={values.email} required name='email' onChange={handleChange} onBlur={handleBlur} placeholder='Email Address' className='form-control bginput chnages' />
                 
                    <div className='row mt-3'>
                      <div className='col-md-6'>
                        <label className='form-label ml-2'>First Name<span className='text-danger'>*</span></label>
                        <input type='text' value={values.firstname} required name='firstname' placeholder='First Name' onChange={handleChange} onBlur={handleBlur} className='form-control bginput chnages' />
                      </div>
                      <div className='col-md-6'>
                  
                      <label className='form-label ml-2'>Last Name<span className='text-danger'>*</span></label>
                        <input type='text' value={values.lastname} required name='lastname' onChange={handleChange} onBlur={handleBlur} placeholder='Last Name' className='form-control bginput chnages' />
                      </div>
                    </div>
                    
                    <div className='col-md-12 p-0 mt-2 inputWrapper'>
                      <label className='form-label ml-2'>Password<span className='text-danger'>*</span></label>
                      <input className='form-control mb-0 bginput chnages' type={!eyes.password?'password':'text'} required value={values.password} name='password' minLength="8" onChange={handleChange} placeholder='Password' onBlur={handleBlur} />
                      <i className={eyes.password ? 'fa fa-eye' : 'fa fa-eye-slash' } onClick={() => setEyes({ ...eyes, password: !eyes.password })}></i>
                    </div>
                    <div>
                    <div className='mt-4 d-flex align-items-baseline'>
                      <input type='checkbox' className='checkBox' name="check" onChange={handleChange} />
                      <label className='clickBox ml-2 mb-0' >By clicking Create account, I agree that I have read and accepted the Terms of Use and Privacy Policy.</label>
                    </div>
                      <button type='submit' className='btn btn-primary loginclass mt-3'>Sign Up</button>
                    </div>
                    <div className='borderCls mt-3'><span className='or'>or</span></div>
                    <div className='text-center d-flex justify-content-center mt-2'>
                <a className='btn btn-outline google_id mr-3 '>
                  <img src='/assets/img/gogle.png' />
                  <span className='google_heading'></span>
                </a>
                <a className='btn btn-outline google_id'>
                  <img src='/assets/img/facebooklogo.png' />
                  <span className='google_heading'></span>
                </a>
                </div>
                   
                    </div>
                    
                    </div>

</div>

                    {/* end tab */}

                 
                   
                  </form>
                )
              }

            </Formik>

          </div>
          </div>
        </div>


      </div>
    </div>
    <ToastContainer position="top-right" />
    </>
  )
}

